<template>
  <div class="roles">
    <breaks first="权限管理" scenter="角色列表"></breaks>
    <el-card class="box-card">
      <div class="inpnt-type">
        <!-- 1-1点击添加按钮弹出对话框 -->
        <el-button type="primary" @click="addUsers=true">添加用户</el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column width="48" type="expand">
          <template slot-scope="props">
            <!-- 一级 -->
            <el-row
              class="firstRows"
              v-for="(item, index) in props.row.children"
              :key="index"
            >
              <!-- 一级 -->
              <el-col :span="5">
                <el-tag effect="dark" closable type="primary">
                  {{ item.authName }}
                </el-tag>
                <i style="margin:0  10px" class="el-icon-caret-right"></i>
              </el-col>
              <!-- 16等分===二级和三级 -->
              <el-col :span="19">
                <!-- 循环的二级 -->
                <el-row
                  class="secondRows"
                  style=""
                  v-for="(item2, index2) in item.children"
                  :key="index2"
                >
                  <el-col :span="5">
                    <el-tag effect="dark" closable type="success">
                      {{ item2.authName }}
                    </el-tag>

                    <i style="margin:0 10px" class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="19">
                    <el-tag
                      effect="dark"
                      style="margin: 10px 10px 10px 0;"
                      v-for="(item3, index3) in item2.children"
                      :key="index3"
                      closable
                      type="warning"
                    >
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column
          label="#"
          type="index"
          width="48">
        </el-table-column>
        <el-table-column label="角色名称" width="441" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" width="440" prop="roleDesc"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="updateByName(scope.row)">编辑</el-button>
            <!-- 删除按钮 -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteById(scope.row)">删除</el-button>
            <!-- 分配权限按钮 -->
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="getTree(scope.row)">分配权限
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="分配权限" :visible.sync="dialogFormVisible" @close="close">
        <el-tree
          :data="data"
          show-checkbox
          node-key="id"
          ref="tree"
          :default-expanded-keys="[2, 3]"
          :default-expand-all="true"
          :default-checked-keys="checkTree"
          :props="defaultProps">
        </el-tree>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible=false">取 消</el-button>
          <el-button type="primary" @click="entSubmit">确 定</el-button>
        </div>
      </el-dialog>
      <!--      添加用户-->
      <el-dialog title="添加商品分类" :visible.sync="addUsers">
        <el-form :model="form" :rules="rules" ref="ruleForm">
          <el-form-item label="角色名称" :label-width="formLabelWidth" prop="roleName">
            <el-input v-model="form.roleName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" :label-width="formLabelWidth" prop="roleDesc">
            <el-input v-model="form.roleDesc" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer" @close="close">
          <el-button @click="addUsers = false">取 消</el-button>
          <el-button type="primary" @click="entAddUser">确 定</el-button>
        </div>
      </el-dialog>
      <!--      修改用户-->
      <el-dialog title="添加商品分类" :visible.sync="flexbolder" @close="close">
        <el-form :model="form1" :rules="rules" ref="ruleForm1">
          <el-form-item label="角色名称" :label-width="formLabelWidth" prop="roleName">
            <el-input v-model="form1.roleName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" :label-width="formLabelWidth" prop="roleDesc">
            <el-input v-model="form1.roleDesc" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="flexbolder = false">取 消</el-button>
          <el-button type="primary" @click="entUpdateUser">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [],
      dialogFormVisible: false,
      addUsers: false,
      flexbolder: false,
      data: [],
      roleId: 0,
      checkTree: [],
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      form: {
        roleName: '',
        roleDesc: ''
      },
      form1: {
        roleName: '',
        roleDesc: ''
      },
      rules: {
        roleName: [
          {
            required: true,
            message: '请输入角色名称',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 8,
            message: '长度在 3 到 8 个字符',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          {
            required: true,
            message: '请输入角色描述',
            trigger: 'blur'
          }
        ]
      },
      formLabelWidth: '80px'
    }
  },
  created () {
    this.getRoles()
  },
  methods: {
    async getRoles () {
      const res = await this.$http('/roles')
      // console.log(res.data)
      this.tableData = res.data.data
    },
    async getTree (row) {
      // console.log(row)
      this.roleId = row.id
      const res = await this.$http('/rights/tree')
      // console.log(res.data)
      this.data = res.data.data
      this.checkout(row, this.checkTree)
      this.dialogFormVisible = true
    },
    checkout (obj, arr) {
      if (!obj.children) {
        this.checkTree.push(obj.id)
        return
      }
      obj.children.forEach(item => {
        this.checkout(item, arr)
      })
    },
    async entSubmit () {
      // console.log(this.$refs.tree.getHalfCheckedKeys())
      // console.log(this.$refs.tree.getCheckedKeys())
      const rids = [...this.$refs.tree.getHalfCheckedKeys(), ...this.$refs.tree.getCheckedKeys()].join(',')
      // console.log(rids)
      // console.log(this.roleId)
      const datas = await this.$http.post(`/roles/${this.roleId}/rights`, {
        rids
      })
      const res = datas.data
      // console.log(res)
      if (res.meta.status === 200) {
        this.$message({
          message: res.meta.msg,
          type: 'success'
        })
        this.dialogFormVisible = false
        this.getRoles()
      } else {
        this.$message.error(res.meta.msg)
      }
    },
    entAddUser () {
      this.$refs.ruleForm.validate(async valid => {
        if (valid) {
          const res = await this.$http.post('/roles', this.form)
          if (res.data.meta.status === 201) {
            this.$message.success(res.data.meta.msg)
            this.addUsers = false
            this.$refs.ruleForm.resetFields()
          } else {
            this.$message.error(res.data.meta.msg)
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 修改角色
    async updateByName (rows) {
      this.flexbolder = true
      console.log(rows)
      this.form1 = rows
    },
    // 提交修改后的角色
    entUpdateUser () {
      this.$refs.ruleForm1.validate(async valid => {
        // console.log(this.form)
        if (valid) {
          const res = await this.$http.put(`/roles/${this.form1.id}`, this.form1)
          console.log(res)
          if (res.data.meta.status === 200) {
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.flexbolder = false
          } else {
            this.$message.error(res.data.meta.msg)
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 删除角色
    deleteById (rows) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const res = await this.$http.delete(`roles/${rows.id}`)
        if (res.data.meta.status === 200) {
          this.$message({
            message: res.data.meta.msg,
            type: 'success'
          })
          this.getRoles()
          this.flexbolder = false
        } else {
          this.$message.error(res.data.meta.msg)
        }
      })
    },
    close () {
      this.checkTree = []
    }
  }
}
</script>

<style lang="less" scoped>
.roles {
  width: 100%;
  height: 100%;

  .el-table {
    margin: 15px 0;

    .firstRows {
      &:nth-child(1) {
        border-top: 1px solid #ccc;
      }

      border: 1px solid #ccc;
      border-top: 0;
      display: flex;
      align-items: center;
      text-align: center;

      .secondRows {
        padding: 10px 0;
        display: flex;
        align-items: center;
        border: 1px solid #ccc;
        border-bottom: 0;

        &:nth-child(1) {
          border-top: 0;
        }

        .el-col-19 {
          text-align: left;
        }
      }
    }
  }

}
</style>
