<template>
  <div class="rights">
    <breaks first="权限管理" scenter="权限列表"></breaks>
    <el-card class="box-card">
      <el-table
        :data="list"
        border
        style="width: 100%">
        <el-table-column
          label="#"
          type="index"
          width="48">
        </el-table-column>
        <el-table-column label="权限名称" width="441" prop="authName"></el-table-column>
        <el-table-column label="路径" width="440" prop="path"></el-table-column>
        <el-table-column label="权限等级">
          <template slot-scope="scoped">
            <el-tag size="mini" v-if="scoped.row.level==='0'">一级</el-tag>
            <el-tag size="mini" v-if="scoped.row.level==='1'" type="success">二级</el-tag>
            <el-tag size="mini" v-if="scoped.row.level==='2'" type="warning">三级</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      list: []
    }
  },
  created () {
    this.getList()
  },
  methods: {
    async getList () {
      const res = await this.$http('/rights/list')
      this.list = res.data.data
    }
  }
}
</script>
